import styled from "styled-components";
import React, {Component} from "react";
import Card from '../cardHOC/card.component';
const Title = styled.h3`
			font-weight:bold;
			font-family: 'Montserrat', sans-serif;
			font-size:3vmax;
			letter-spacing:0.1vw;
			${props=> props.center ? "text-align: center;" : "text-align: left;"}
	`;
const Count = styled.h3`
			font-weight:bold;
			font-family: 'Montserrat', sans-serif;
			font-size:4.54vmax;
			margin:0;
	`;
const CountBlock = styled.div`
			color:#1c4e45;
			display:flex;
			height:min-content;
			& span{
			 font-weight:400;
			 margin-top:auto;
			 margin-bottom:1vmax;
			}
			margin-left:auto;
	`;
const Block = styled.div`
		display:flex;
		flex-direction:column;
		width:80%;
		margin:auto;
	`;

class TotalCount extends Component {
	state = {
		count: "",
		isError: false,
	};
	
	componentDidMount() {
		fetch('https://statbotservis.glitch.me/count').then(response => response.json())
			.then(data => {
				this.setState({count:data});
			})
			.catch((e)=>{
				this.setState({isError:true});
			})
	}
	componentDidCatch(error, errorInfo) {
		this.setState({isError:true});
	}
	
	render()
		{
			const {isError} = this.state;
			
			
			
			
			return (
				<React.Fragment>
					{
						!isError && (
							<Card>
								<Block>
									<Title>Total count:</Title>
									<CountBlock><Count>{this.state.count}</Count><span> users</span></CountBlock>
								</Block>
								
							</Card>)
					}
					{
						isError && (
						<Card isError><Block>
							<Title center>Error</Title>
						</Block>
						</Card>)
					}
				</React.Fragment>
			
			)
		}
	}
	
	export 	default	TotalCount;